import React from 'react';
import { Consumer } from './Context';

class AddProject extends React.Component {
  constructor(props) {
    super(props);
    this.nameRef = React.createRef();
    this.rateRef = React.createRef();
    this.dateRef = React.createRef();
    this.hourRef = React.createRef();
  }

  render() {
    return (
      <Consumer>
        {context => {
          
          createProject = e => {
            e.preventDefault();
            const project = {
              projectName: this.nameRef.current.value,
              hourlyRate: this.rateRef.current.value,
              deadlineDate: this.dateRef.current.value,
              hoursToComplete: this.hourRef.current.value,
            };
            const projects = { ...context.projects };
            projects[`project${Date.now()}`] = project;
            this.setState({ projects });
            e.currentTarget.reset();
          };

          return (
            <form action="/" onSubmit={this.createProject}>
              <label>
                projectName
                <input type="text" name="projectName" ref={this.nameRef} />
              </label>

              <label>
                hourlyRate
                <input type="number" name="hourlyRate" ref={this.rateRef} />
              </label>

              <label>
                deadlineDate
                <input type="date" name="deadlineDate" ref={this.dateRef} />
              </label>

              <label>
                hoursToComplete
                <input
                  type="number"
                  name="hoursToComplete"
                  ref={this.hourRef}
                />
              </label>

              <button type="submit">Submit</button>
            </form>
          );
        }}
      </Consumer>
    );
  }
}

export default AddProject;
